.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.App {
  height: 100vh;
}

form {
  position: relative;
  width: 350px;
  margin: 0 auto;
  background: white;
  box-shadow: 0px 4px 19px 0px #9d9d9d;
  padding: 20px;
  border-radius: 10px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 15px;
  padding: 0 30px;
}

form input {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 350px;
  height: 20px;
  border-radius: 6px;
  border: 0;
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: 600;
}

form input:focus {
  outline: none;
}

input::placeholder {
  font-size: 18px;
  font-weight: 600;
  font-family: system-ui;
  color: black;
}

textarea::placeholder {
  font-size: 14px;
  font-family: system-ui;
  color: black;
}

form textarea {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 350px;
  border-radius: 6px;
  padding-top: 8px;
  border: 0;
  resize: none;
}

button.add_btn {
  position: absolute !important;
  right: 0 !important;
  bottom: 14px !important;
}

form textarea:focus {
  outline: none;
}

.main-note {
  margin: 30px 0;
}

.plus_icon {
  color: white;
  background: #000;
  border-radius: 50%;
  width: 30px !important;
  height: 30px !important;
  transition: 0.5s;
}

.close_btn {
  color: white !important;
  background: #000 !important;
  width: 30px !important;
  height: 30px !important;
  margin-left: 13rem !important;
  display: block !important;
  line-height: 10px !important;
  padding: 6px !important;
  border: 2px solid black !important
}

.close_btn:hover {
  color: black !important;
  background: white !important; 
  transition: 0.5s !important;
}

.plus_icon:hover {
  color: black;
  background: white;
  border: 2px solid black;
  transition: 0.5s;
}

.note {
  display: flex;
  align-items: start;
  text-align: start;
  margin-bottom: 100px;
}

.note .card {
  border-radius: 8px;
  box-shadow: 0px 4px 19px 0px #9d9d9d;
  padding: 8px 12px;
}

.note .card h1 {
  font-size: 30px;
  font-weight: 600;
  font-family: sans-serif;
  margin-bottom: 10px;
}

.card p {
  font-size: 15px;
  font-family: monospace;
  font-weight: 500;
}

.card .delete_btn {
  margin-left: auto !important;
  display: block;
  margin-top: 13px;
  background: transparent;
  border: 0;
  padding: 0;
}

